<template>
  <div id="provincial"></div>
</template>

<script>
import mapData from "@/assets/GeographicData/provincial.js";
export default {
  mounted() {
    let that = this;
    this.echarts.registerMap("SC", mapData);
    let Chart = this.echarts.init(document.getElementById("provincial"));
    let option = {
      get: {
        name: "SC PopEstimates",
        type: "map",
        aspectScale: 1, // 横向拉伸
        // roam: true, // 地图操作 开启缩放或者平移，可以设置成 'scale' 或者 'move'。
        map: "SC",
        label: {
          show: true,
          normal: {
            show: true, // 默认地图文字字号和字体颜色
            fontSize: 12,
            color: "#CFCFCF",
          },
          emphasis: {
            show: true,
            fontSize: 12, // 选中地图文字字号和字体颜色
            color: "#CFCFCF",
          },
        },
        itemStyle: {
          normal: {
            areaColor: "#000606", //地图本身的颜色
            borderColor: "#7E8080", //省份边框颜色
            borderWidth: 7, // 省份边框宽度
            opacity: 1, //图形透明度
          },
          emphasis: {
            areaColor: "#CD2B29", // 高亮时候地图显示的颜色
            borderWidth: 0, // 高亮时的边框宽度
          },
        },
        textFixed: {
          Alaska: [20, -20],
        },
      },
      geo: {
        type: "map",
        aspectScale: 1, // 横向拉伸
        // roam: true, // 地图操作 开启缩放或者平移，可以设置成 'scale' 或者 'move'。
        map: "SC",
        label: {
          show: true,
          normal: {
            show: true, // 默认地图文字字号和字体颜色
            fontSize: 12,
            color: "#CFCFCF",
          },
          emphasis: {
            show: true,
            fontSize: 12, // 选中地图文字字号和字体颜色
            color: "#CFCFCF",
          },
        },
        itemStyle: {
          normal: {
            areaColor: "#000606", //地图本身的颜色
            borderColor: "#7E8080", //省份边框颜色
            borderWidth: 1, // 省份边框宽度
            opacity: 1, //图形透明度
          },
          emphasis: {
            areaColor: "#CD2B29", // 高亮时候地图显示的颜色
            borderWidth: 0, // 高亮时的边框宽度
          },
        },
        textFixed: {
          Alaska: [20, -20],
        },
        // 地图区域颜色
        regions: that.regions,
      },
      series: [
        {
          type: "effectScatter",
          coordinateSystem: "geo",
          symbolSize: 12,
          label: {
            normal: {
              show: false,
            },
            emphasis: {
              show: false,
            },
          },
          itemStyle: {
            normal: {
              shadowBlur: 10,
              color: "#00ECC8",
            },
            emphasis: {
              borderColor: "#fff",
              borderWidth: 1,
            },
          },
        },
      ],
    };
    Chart.setOption(option);
    Chart.on("click", function (params) {
      that.$router.push({
        path: "/city",
        query: { name: params.name },
      });
    });
  },
  data() {
    return {
      regions: [
        {
          // 重点 部分 ,在这里给大家模拟一个省份颜色与界线颜色的修改,如果想修改多个省份就在后面多添加几个对象即可.
          name: "成都市", // 对应的是import "./china"  数据中的名称如: name: "广东省"(下面有截图)
          itemStyle: {
            normal: {
              opacity: 1, // 透明度
              borderColor: "#fff", // 省份界线颜色
              borderWidth: 3, // 省份界线的宽度
              areaColor: "red", // 整个省份的颜色
            },
          },
        },
      ],
    };
  },
};
</script>

<style scoped>
#provincial {
  height: 100%;
  width: 100%;
}
</style>
